import React, { useState, useContext, useEffect } from "react";
// import MyHeader from "./components/header";
import { makeStyles } from "@material-ui/core/styles";
import LabelBottomNavigation from "./components/footer";
import { UserContext } from "./App";
import Paper from "@material-ui/core/Paper";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";

const useStyles = makeStyles((theme) => ({
  video: {
    width: "50%",
    marginLeft: "auto",
    marginRight: "auto",
    marginTop: "50px",
    display: "block",
  },
  headers: {
    textAlign: "center",
    marginTop: "100px",
    fontFamily: "Mate SC",
  },
  subheaders: {
    textAlign: "center",
    fontFamily: "Mate SC",
    marginTop: "20px",
  },
  table: {
    minWidth: 650,
  },
  tableCon: {
    width: "90%",
    marginLeft: "auto",
    marginRight: "auto",
  },
  root: {
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center",
    padding: theme.spacing(2),
    marginTop: "5%",

    "& .MuiTextField-root": {
      margin: theme.spacing(1),
      width: "300px",
    },
    "& .MuiButtonBase-root": {
      margin: theme.spacing(2),
    },
  },
  logoutBtn: {
    width: "70%",
  },
}));

function User(props) {
  //   const env = dotenv.config();
  const { REACT_APP_API_URL } = process.env;
  const classes = useStyles();
  const { user, setUser } = useContext(UserContext);
  const [showEdit, setShowEdit] = useState(false);
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [success, setSuccess] = useState("none");
  console.log(user);
  console.log(REACT_APP_API_URL);

  const setUpUser = () => {
    setName(user.name);
    setEmail(user.email);
    setPassword("");
  };

  useEffect(setUpUser, []);

  const handleClear = () => {
    setName("");
    setEmail("");
    setPassword("");
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    fetch(`${REACT_APP_API_URL}/api/users/reset`, {
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        Authorization: `Bearer ${user.token}`,
      },
      method: "POST",
      body: JSON.stringify({
        name: name,
        email: email,
        password: password,
      }),
    })
      .then((res) =>
        res.json().then((data) => {
          data.role ? setSuccess("true") : setSuccess("false");
          data.message && console.log(data.message);
          console.log(data);
        })
      )
      .catch((err) => console.log(err));
  };

  return (
    <React.Fragment>
      <h2 className={classes.headers}>User Profile</h2>
      <form className={classes.root} onSubmit={handleSubmit}>
        <h5 style={{ fontFamily: "Kalam", color: "black" }}>Your Info</h5>
        <TextField
          label="Name"
          variant="filled"
          required
          value={name}
          onChange={(e) => setName(e.target.value)}
          disabled={!showEdit}
        />
        <TextField
          label="Email"
          variant="filled"
          type="email"
          required
          autoComplete="email"
          id="email"
          name="email"
          autoFocus
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          disabled={!showEdit}
        />
        <TextField
          label="Password"
          variant="filled"
          type="password"
          required
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          disabled={!showEdit}
        />
        {password.length < 8 && password.length !== 0 && (
          <p style={{ color: "red" }}>Password must have at least 8 words</p>
        )}
        <div>
          <Button variant="contained" onClick={handleClear}>
            Clear
          </Button>
          {showEdit && (
            <Button type="submit" variant="contained" color="primary">
              Submit
            </Button>
          )}
          {!showEdit && (
            <Button
              variant="contained"
              color="secondary"
              onClick={() => {
                setShowEdit(true);
              }}
            >
              Edit
            </Button>
          )}
        </div>
        {success === "true" && (
          <p style={{ color: "Blue" }}>Information Updated successfully</p>
        )}
        {success === "false" && (
          <p style={{ color: "Red" }}>Update not successful</p>
        )}
      </form>
      <div className={classes.subheaders}>
        {" "}
        <Button
          className={classes.logoutBtn}
          variant="contained"
          color="secondary"
          onClick={() => {
            handleClear();
            setUser({
              role: "",
              token: "",
              email: "",
              name: "",
              id: "",
              cart: [],
            });
          }}
        >
          Log Out
        </Button>
      </div>
      <LabelBottomNavigation />
    </React.Fragment>
  );
}

export default User;
