import React, { useState, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import LabelBottomNavigation from "./components/footer";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center",
    padding: theme.spacing(2),

    "& .MuiTextField-root": {
      margin: theme.spacing(1),
      width: "300px",
    },
    "& .MuiButtonBase-root": {
      margin: theme.spacing(2),
    },
  },
  headers: {
    textAlign: "center",
    marginTop: "100px",
    fontFamily: "Mate SC",
    paddingBottom: "5%",
    width: "80%",
    marginLeft: "auto",
    marginRight: "auto",
  },
  myBtn: {
    marginRight: "10px",
  },
  panel: {
    width: "80%",
    marginLeft: "auto",
    marginRight: "auto",
  },
  alert: {
    color: "red",
  },
}));

function Admin(props) {
  const classes = useStyles();
  const [displayAddProduct, setDisplayAddProduct] = useState(false);
  const [displayEditProduct, setDisplayEditProduct] = useState(false);
  const [displayDeleteProduct, setDisplayDeleteProduct] = useState(false);
  const [displayCarts, setDisplayCarts] = useState(false);
  const [name, setName] = useState("");
  const [imgURL, setImgURL] = useState("");
  const [price, setPrice] = useState("");
  const [category, setCategory] = useState("");
  const [id, setId] = useState("");
  const [addAlert, setAddAlert] = useState(false);
  const [editAlert, setEditAlert] = useState(false);
  const [deleteAlert, setDeleteAlert] = useState(false);
  const [products, setProducts] = useState([]);
  const [carts, setCarts] = useState([]);
  const { REACT_APP_API_URL } = process.env;

  function getAllProducts() {
    fetch(`${REACT_APP_API_URL}/api/products`, {
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      method: "GET",
    })
      .then((res) =>
        res.json().then((data) => {
          setProducts(data);
          console.log(data);
        })
      )
      .catch((err) => console.log(err));
  }

  function getAllCarts() {
    fetch(`${REACT_APP_API_URL}/api/carts`, {
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      method: "GET",
    })
      .then((res) =>
        res.json().then((data) => {
          setCarts(data);
          console.log(data);
        })
      )
      .catch((err) => console.log(err));
  }

  useEffect(() => {
    getAllCarts();
    getAllProducts();
  }, []);

  const clearDisplay = () => {
    setDisplayAddProduct(false);
    setDisplayCarts(false);
    setDisplayDeleteProduct(false);
    setDisplayEditProduct(false);
  };

  const handleClear = () => {
    setId("");
    setName("");
    setImgURL("");
    setPrice("");
    setCategory("");
    setAddAlert(false);
    setEditAlert(false);
    setDeleteAlert(false);
  };

  const showAddProduct = () => {
    clearDisplay();
    setDisplayAddProduct(true);
  };

  const showEditProduct = () => {
    clearDisplay();
    setDisplayEditProduct(true);
  };

  const showDeleteProduct = () => {
    clearDisplay();
    setDisplayDeleteProduct(true);
  };

  const showCarts = () => {
    clearDisplay();
    setDisplayCarts(true);
  };

  const handleSubmitAddProduct = (e) => {
    e.preventDefault();
    fetch(`${REACT_APP_API_URL}/api/products/`, {
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      method: "POST",
      body: JSON.stringify({
        name,
        imgURL,
        price,
        category,
        comments: [],
      }),
    })
      .then((res) => {
        console.log(res);
        setAddAlert(true);
      })
      .catch((err) => console.log(err));
  };

  const handleSubmitEditProduct = (e) => {
    e.preventDefault();
    let myComment = products.find((x) => (x.name = name)).comments;
    fetch(`${REACT_APP_API_URL}/api/products/${id}`, {
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      method: "POST",
      body: JSON.stringify({
        name,
        imgURL,
        price,
        category,
        comments: myComment,
      }),
    })
      .then((res) => {
        console.log(res);
        setEditAlert(true);
      })
      .catch((err) => console.log(err));
  };

  const handleSubmitDeleteProduct = (e) => {
    e.preventDefault();
    fetch(`${REACT_APP_API_URL}/api/products/${id}`, {
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      method: "DELETE",
      body: JSON.stringify({
        name,
        imgURL,
        price,
        category,
      }),
    })
      .then((res) => {
        console.log(res);
        setDeleteAlert(true);
      })
      .catch((err) => console.log(err));
  };

  return (
    <React.Fragment>
      {/* <h2 className={classes.headers}>Your items in cart</h2> */}
      <div className={classes.headers}>
        <Button
          className={classes.myBtn}
          variant="contained"
          color="primary"
          onClick={showAddProduct}
        >
          add product
        </Button>
        <Button
          className={classes.myBtn}
          variant="contained"
          color="primary"
          onClick={showEditProduct}
        >
          edit product
        </Button>
        <Button
          className={classes.myBtn}
          variant="contained"
          color="secondary"
          onClick={showDeleteProduct}
        >
          delete product
        </Button>
        <Button
          className={classes.myBtn}
          variant="contained"
          color="default"
          onClick={showCarts}
        >
          Show All Customer Carts
        </Button>
      </div>
      {displayAddProduct && (
        <div className={classes.panel}>
          <form className={classes.root} onSubmit={handleSubmitAddProduct}>
            <h5>Add Product</h5>
            <TextField
              label="Name"
              variant="filled"
              required
              value={name}
              onChange={(e) => setName(e.target.value)}
            />
            <TextField
              label="imgURL"
              variant="filled"
              required
              value={imgURL}
              onChange={(e) => setImgURL(e.target.value)}
            />
            <TextField
              label="price"
              variant="filled"
              required
              value={price}
              onChange={(e) => setPrice(e.target.value)}
            />
            <TextField
              label="category"
              variant="filled"
              required
              value={category}
              onChange={(e) => setCategory(e.target.value)}
            />
            <div>
              <Button variant="contained" onClick={handleClear}>
                Clear
              </Button>
              <Button type="submit" variant="contained" color="primary">
                Add
              </Button>
            </div>
            {addAlert && (
              <>
                <h5 className={classes.alert}>Product added successfully.</h5>
                <h5 className={classes.alert}>
                  Please press "Clear" to add another.
                </h5>
              </>
            )}
          </form>
        </div>
      )}
      {displayEditProduct && (
        <div className={classes.panel}>
          <form className={classes.root} onSubmit={handleSubmitEditProduct}>
            <h5>Edit Product</h5>
            <TextField
              label="Id"
              variant="filled"
              required
              value={id}
              onChange={(e) => setId(e.target.value)}
            />
            <TextField
              label="Name"
              variant="filled"
              required
              value={name}
              onChange={(e) => setName(e.target.value)}
            />
            <TextField
              label="imgURL"
              variant="filled"
              required
              value={imgURL}
              onChange={(e) => setImgURL(e.target.value)}
            />
            <TextField
              label="price"
              variant="filled"
              required
              value={price}
              onChange={(e) => setPrice(e.target.value)}
            />
            <TextField
              label="category"
              variant="filled"
              required
              value={category}
              onChange={(e) => setCategory(e.target.value)}
            />
            <div>
              <Button variant="contained" onClick={handleClear}>
                Clear
              </Button>
              <Button type="submit" variant="contained" color="primary">
                Edit
              </Button>
            </div>
            {editAlert && (
              <>
                <h5 className={classes.alert}>Product editted successfully.</h5>
                <h5 className={classes.alert}>
                  Please press "Clear" to Edit another.
                </h5>
              </>
            )}
          </form>
        </div>
      )}
      {displayDeleteProduct && (
        <div className={classes.panel}>
          <form className={classes.root} onSubmit={handleSubmitDeleteProduct}>
            <h5>Enter ID to Delete Product</h5>
            <TextField
              label="Id"
              variant="filled"
              required
              value={id}
              onChange={(e) => setId(e.target.value)}
            />
            <div>
              <Button variant="contained" onClick={handleClear}>
                Clear
              </Button>
              <Button type="submit" variant="contained" color="secondary">
                Delete
              </Button>
            </div>
            {deleteAlert && (
              <>
                <h5 className={classes.alert}>Product deleted successfully.</h5>
                <h5 className={classes.alert}>
                  Please press "Clear" to Delete another.
                </h5>
              </>
            )}
          </form>
        </div>
      )}
      {displayCarts && (
        <div className={classes.panel}>
          <TableContainer component={Paper} className={classes.tableCon}>
            <Table className={classes.table} aria-label="simple table">
              <TableHead>
                <TableRow>
                  <TableCell align="right">_id</TableCell>
                  <TableCell align="right">user</TableCell>
                  <TableCell align="right">product</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {carts.map((row) => (
                  <TableRow key={row._id}>
                    <TableCell component="th" scope="row">
                      {row._id}
                    </TableCell>
                    <TableCell align="right">{row.user}</TableCell>
                    <TableCell align="right">
                      {row.product.map((x) => (
                        <Paper key={x._id}>Name: {x.name}</Paper>
                      ))}
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </div>
      )}
      <LabelBottomNavigation />
    </React.Fragment>
  );
}

export default Admin;
