import React, { useState, useEffect, useContext } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import { Paper } from "@material-ui/core";
import TextField from "@material-ui/core/TextField";

import { UserContext } from "./../App";

const useStyles = makeStyles((theme) => ({
  root: {
    // maxWidth: 345,
  },
  media: {
    maxWidth: 345,
    height: 0,
    paddingTop: "20.25%",
    marginLeft: "auto",
    marginRight: "auto",
  },
  usd: {
    ...theme.typography.button,
    backgroundColor: theme.palette.background.paper,
    padding: theme.spacing(1),
  },
}));

export default function MediaCard(props) {
  const classes = useStyles();
  const [showComment, setShowComment] = useState(false);
  const [rating, setRating] = useState("");
  const [recommended, setRecommended] = useState("");
  const [comment, setComment] = useState("");
  const [commentAdded, setCommentAdded] = useState(false);
  const [showAdd, setShowAdd] = useState(false);
  const { user, setUser } = useContext(UserContext);
  const [showCart, setShowCart] = useState(false);
  const { REACT_APP_API_URL } = process.env;

  function renderComments() {
    return props.comments.map((x) => (
      <CardContent key={x.comment}>
        <Paper style={{ padding: "20px" }}>
          Recommended: {x.recommended} <br />
          Rating: {x.rating} <br />
          Comment: {x.comment} <br />
        </Paper>
        <br />
      </CardContent>
    ));
  }

  function handleCommentSubmit(e) {
    e.preventDefault();
    let newComments = props.comments.concat({
      rating: rating,
      recommended: recommended,
      comment: comment,
    });
    console.log(newComments);
    fetch(`${REACT_APP_API_URL}/products/${props.id}`, {
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      method: "POST",
      body: JSON.stringify({
        name: props.name,
        imgURL: props.url,
        price: props.price,
        category: props.category,
        comments: newComments,
      }),
    })
      .then(console.log("comment added"))
      .then(setCommentAdded(true))
      .catch((e) => console.log(e));
  }

  function addToCart(name, price, category, id) {
    let newCart = user.cart[0].product.push({
      name,
      price,
      category,
      id,
    });
    console.log(user.cart[0].product);
    fetch(`${REACT_APP_API_URL}/api/carts/myCart/`, {
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        Authorization: `Bearer ${user.token}`,
      },
      method: "POST",
      body: JSON.stringify({
        user: user._id,
        product: user.cart[0].product,
      }),
    })
      .then(function (res) {
        res.json().then((data) => {
          !data.message && setUser((prev) => ({ ...prev, cart: [data] }));
          setShowCart(true);
          console.log(user);
        });
      })
      .catch(function (res) {
        console.log(res);
      });
  }

  function renderAdd() {
    return (
      <>
        <hr />
        <CardContent>
          <p>Add comment:</p>
          <form className={classes.root} onSubmit={handleCommentSubmit}>
            <TextField
              label="rating"
              variant="filled"
              required
              value={rating}
              onChange={(e) => setRating(e.target.value)}
            />
            <TextField
              label="recommended"
              variant="filled"
              required
              autoFocus
              value={recommended}
              onChange={(e) => setRecommended(e.target.value)}
            />
            <TextField
              label="comment"
              variant="filled"
              required
              value={comment}
              onChange={(e) => setComment(e.target.value)}
            />
            <div>
              <br />
              <Button type="submit" variant="contained" color="primary">
                Submit
              </Button>
            </div>
            {commentAdded && (
              <p style={{ color: "red" }}>Refresh to see your comment</p>
            )}
          </form>
        </CardContent>
      </>
    );
  }

  return (
    <Card className={classes.root}>
      <CardActionArea>
        <CardMedia className={classes.media} image={props.url} />
        <CardContent>
          <Typography gutterBottom variant="h5" component="h2">
            {props.name}
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p">
            {props.category}
          </Typography>
          <Typography className={classes.root}>USD ${props.price}</Typography>
        </CardContent>
      </CardActionArea>
      <CardActions>
        {user.name !== "" && (
          <>
            <Button
              size="small"
              onClick={() => {
                addToCart(props.name, props.price, props.category, props.id);
              }}
            >
              Add to cart
            </Button>
            <br></br>
          </>
        )}
        <Button
          size="small"
          onClick={() => {
            setShowComment((prev) => !prev);
          }}
        >
          Comments
        </Button>
        {user.name !== "" && (
          <>
            <Button
              onClick={() => {
                setShowAdd((prev) => !prev);
              }}
            >
              Add Comment
            </Button>
            <br></br>
          </>
        )}
      </CardActions>
      {showComment && renderComments()}
      {showAdd && renderAdd()}
      {showCart && (
        <CardContent>
          <p style={{ color: "red" }}>Item added to cart</p>
        </CardContent>
      )}
    </Card>
  );
}
