import React, { useState } from "react";
import { makeStyles } from "@material-ui/core";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center",
    padding: theme.spacing(2),
    marginTop: "10%",

    "& .MuiTextField-root": {
      margin: theme.spacing(1),
      width: "300px",
    },
    "& .MuiButtonBase-root": {
      margin: theme.spacing(2),
    },
  },
}));

const Form = () => {
  const classes = useStyles();
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [success, setSuccess] = useState("none");
  const { REACT_APP_API_URL } = process.env;

  const handleClear = () => {
    setName("");
    setEmail("");
    setPassword("");
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    fetch(`${REACT_APP_API_URL}/api/users/register`, {
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      method: "POST",
      body: JSON.stringify({
        name: name,
        email: email,
        password: password,
      }),
    })
      .then(
        (res) => (res.status === 200 ? setSuccess("true") : setSuccess("false"))
        // .json()
        // .then((data) =>
        //   data.role ? setSuccess("true") : setSuccess("false")
        // )
      )
      .catch((err) => console.log(err));
  };

  return (
    <form className={classes.root} onSubmit={handleSubmit}>
      <h5 style={{ fontFamily: "Kalam", color: "black" }}>Sign up</h5>
      <TextField
        label="Name"
        variant="filled"
        required
        value={name}
        onChange={(e) => setName(e.target.value)}
      />
      <TextField
        label="Email"
        variant="filled"
        type="email"
        required
        autoComplete="email"
        id="email"
        name="email"
        autoFocus
        value={email}
        onChange={(e) => setEmail(e.target.value)}
      />
      <TextField
        label="Password"
        variant="filled"
        type="password"
        required
        value={password}
        onChange={(e) => setPassword(e.target.value)}
      />
      {password.length < 8 && password.length !== 0 && (
        <p style={{ color: "red" }}>Password must have at least 8 words</p>
      )}
      <div>
        <Button variant="contained" onClick={handleClear}>
          Clear
        </Button>
        <Button type="submit" variant="contained" color="primary">
          Signup
        </Button>
      </div>
      {success === "true" && (
        <p style={{ color: "Blue" }}>Sign up successful. Go to login page.</p>
      )}
      {success === "false" && (
        <p style={{ color: "Blue" }}>
          Sign up not successful. Please refresh and try again.
        </p>
      )}
    </form>
  );
};

export default Form;
