import React, { useState } from "react";
// import MyHeader from "./components/header";
import { makeStyles } from "@material-ui/core/styles";
import video from "./model.mp4";
import LabelBottomNavigation from "./components/footer";
import Form from "react-bootstrap/Form";

const useStyles = makeStyles((theme) => ({
  message: {
    marginTop: "10px",
  },
}));

function Message(props) {
  const classes = useStyles();
  const [message, setMessage] = useState(false);
  const showMessage = () => {
    setMessage(true);
  };

  return (
    <React.Fragment>
      <section className=" ml-5 mr-5 pl-5 pr-5">
        <h2 className="h1-responsive font-weight-bold text-center my-4">
          Contact us
        </h2>
        <h4 className="text-center w-responsive mx-auto mb-5">
          Do you have any questions? Please do not hesitate to contact us
          directly. Our team will come back to you within a matter of hours to
          help you.
        </h4>

        <div className="row">
          <div className="col-md-9 mb-md-0 mb-5">
            <Form
              id="contact-form"
              name="contact-form"
              action="mail.php"
              method="POST"
            >
              <div className="row">
                <div className="col-md-6">
                  <div className="md-form mb-0">
                    <input
                      type="text"
                      id="name"
                      name="name"
                      className="form-control"
                    />
                    <label for="name">Your name</label>
                  </div>
                </div>
                <div className="col-md-6">
                  <div className="md-form mb-0">
                    <input
                      type="text"
                      id="email"
                      name="email"
                      className="form-control"
                    />
                    <label for="email" className="">
                      Your email
                    </label>
                  </div>
                </div>
              </div>
              <div className="row">
                <div className="col-md-12">
                  <div className="md-form mb-0">
                    <input
                      type="text"
                      id="subject"
                      name="subject"
                      className="form-control"
                    />
                    <label for="subject" className="">
                      Subject
                    </label>
                  </div>
                </div>
              </div>
              <div className="row">
                <div className="col-md-12">
                  <div className="md-form">
                    <textarea
                      type="text"
                      id="message"
                      name="message"
                      rows="2"
                      className="form-control md-textarea"
                    ></textarea>
                    <label for="message">Your message</label>
                  </div>
                </div>
              </div>
            </Form>

            <div className="text-center text-md-left">
              <a className="btn btn-primary" onClick={showMessage}>
                Send
              </a>
            </div>
            {message && (
              <div className="text-center text-md-left mt-2">
                Thank you for getting in touch! We will contact you very soon!
              </div>
            )}
            <div className="status"></div>
          </div>
          <div className="col-md-3 text-center">
            <ul className="list-unstyled mb-0">
              <li>
                <i className="fas fa-map-marker-alt fa-2x"></i>
                <p>San Francisco, CA 94126, USA</p>
              </li>

              <li>
                <i className="fas fa-phone mt-4 fa-2x"></i>
                <p>+ 01 234 567 89</p>
              </li>

              <li>
                <i className="fas fa-envelope mt-4 fa-2x"></i>
                <p>contact@superdamp.com</p>
              </li>
            </ul>
          </div>
        </div>
      </section>
      <LabelBottomNavigation />
    </React.Fragment>
  );
}

export default Message;
