import React, { useState, useEffect } from "react";
// import MyHeader from "./components/header";
import { makeStyles } from "@material-ui/core/styles";
import video from "./model.mp4";
import LabelBottomNavigation from "./components/footer";
import Card from "./components/cards";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";

const useStyles = makeStyles((theme) => ({
  video: {
    width: "50%",
    marginLeft: "auto",
    marginRight: "auto",
    marginTop: "50px",
    display: "block",
  },
  headers: {
    textAlign: "center",
    marginTop: "100px",
    fontFamily: "Mate SC",
    paddingBottom: "60px",
  },
  root: {
    flexGrow: 1,
    marginLeft: "80px",
    marginRight: "80px",
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: "center",
    color: theme.palette.text.secondary,
  },
  myBtn: {
    marginRight: "20px",
    marginBottom: "15px",
  },
}));

function Product(props) {
  const classes = useStyles();
  const [products, setProducts] = useState([]);
  const [displayProducts, setdisplayProducts] = useState([]);
  const [refresh, setRefresh] = useState(true);
  const { REACT_APP_API_URL } = process.env;

  function getAllProducts() {
    fetch(`${REACT_APP_API_URL}/api/products`, {
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      method: "GET",
    })
      .then((res) =>
        res.json().then((data) => {
          setProducts(data);
          setdisplayProducts(data);
          console.log(data);
        })
      )
      .catch((err) => console.log(err));
  }

  useEffect(getAllProducts, []);

  const getJackets = () => {
    let my = products.filter((x) => x.category === "Jackets");
    console.log(my);
    setdisplayProducts(my);
  };
  const getPants = () => {
    let my = products.filter((x) => x.category === "Pants");
    console.log(my);
    setdisplayProducts(my);
  };
  const getHoodies = () => {
    let my = products.filter((x) => x.category === "Hoodies");
    console.log(my);
    setdisplayProducts(my);
  };
  const getTShirts = () => {
    let my = products.filter((x) => x.category === "T-Shirts");
    console.log(my);
    setdisplayProducts(my);
  };
  const getAll = () => {
    setdisplayProducts(products);
  };

  useEffect(() => {
    console.log(products);
  });

  return (
    <React.Fragment>
      {/* <MyHeader></MyHeader> */}
      <h2 className={classes.headers}>This is product page</h2>
      <div className={classes.root}>
        <div>
          <Button
            className={classes.myBtn}
            variant="contained"
            onClick={getAll}
          >
            Show All
          </Button>
          <Button
            className={classes.myBtn}
            variant="contained"
            onClick={getJackets}
          >
            Show Jackets
          </Button>
          <Button
            className={classes.myBtn}
            variant="contained"
            onClick={getTShirts}
          >
            Show T-Shirts
          </Button>
          <Button
            className={classes.myBtn}
            variant="contained"
            onClick={getHoodies}
          >
            Show Hoodies
          </Button>
          <Button
            className={classes.myBtn}
            variant="contained"
            onClick={getPants}
          >
            Show Pants
          </Button>
        </div>
        <Grid container spacing={3}>
          {displayProducts.map((x) => (
            <Grid item xs={12} sm={12} key={x.name}>
              <Card
                key={x.name}
                url={x.imgURL}
                name={x.name}
                category={x.category}
                price={x.price}
                comments={x.comments}
                id={x._id}
                refresh={setRefresh}
              />
            </Grid>
          ))}
        </Grid>
      </div>
      <LabelBottomNavigation />
    </React.Fragment>
  );
}

export default Product;
