import React, { useState, useEffect, useContext } from "react";
import { makeStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";
import IconButton from "@material-ui/core/IconButton";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faShoppingCart } from "@fortawesome/free-solid-svg-icons";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import Home from "./../Home";
import Product from "./../Product";
import Message from "./../Message";
import { ShoppingCart } from "@material-ui/icons";
import Cart from "./../ShoppingChart";
import Login from "./login";
import { UserContext } from "./../App";
import Signup from "./signup";
import Admin from "./../admin";
import User from "./../User";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  fontBar: {
    fontFamily: "Kalam",
    color: "black",
  },
  fontHeader: {
    fontFamily: "Sacramento",
    fontSize: "1.5rem",
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
  headerBtn: {
    marginLeft: "10px",
  },
}));

export default function MyHeader() {
  const classes = useStyles();
  const [display, setDisplay] = useState(false);
  const { user, setUser } = useContext(UserContext);
  console.log(user);

  return (
    <Router>
      <div className={classes.root}>
        <AppBar position="fixed" color="default">
          <Toolbar>
            <IconButton
              edge="start"
              className={classes.menuButton}
              color="inherit"
              aria-label="menu"
            ></IconButton>
            <Typography
              variant="h6"
              className={`${classes.title} ${classes.fontHeader}`}
            >
              SuperDamp
            </Typography>
            <Button color="inherit" className={`${classes.headerBtn}`}>
              <Link to="/" className={`${classes.fontBar}`}>
                Home
              </Link>
            </Button>
            <Button color="inherit" className={`${classes.headerBtn}`}>
              <Link to="/product" className={`${classes.fontBar}`}>
                Product
              </Link>
            </Button>
            <Button color="inherit" className={`${classes.headerBtn}`}>
              <Link to="/Message" className={`${classes.fontBar}`}>
                Message Us
              </Link>
            </Button>
            <Button color="inherit" className={`${classes.headerBtn}`}>
              <Link to="/cart" className={`${classes.fontBar}`}>
                cart
              </Link>
            </Button>
            {user.role && user.role !== "user" && (
              <Button color="inherit" className={`${classes.headerBtn}`}>
                <Link to="/admin" className={`${classes.fontBar}`}>
                  Admin
                </Link>
              </Button>
            )}
            {user.name === "" ? (
              <Button
                color="inherit"
                className={classes.fontBar}
                variant="contained"
              >
                <Link to="/Login" className={`${classes.fontBar}`}>
                  Login
                </Link>
              </Button>
            ) : (
              <Button
                color="inherit"
                className={classes.fontBar}
                variant="contained"
              >
                <Link to="/user" className={`${classes.fontBar}`}>
                  Welcome {user.name}
                </Link>
              </Button>
            )}
          </Toolbar>
        </AppBar>
        <Switch>
          <Route path="/Login">
            <Login />
          </Route>
          <Route path="/product">
            <Product />
          </Route>
          <Route path="/message">
            <Message />
          </Route>
          <Route path="/cart">
            <Cart />
          </Route>
          <Route path="/admin">
            <Admin />
          </Route>
          <Route path="/signup">
            <Signup />
          </Route>
          <Route path="/user">
            <User />
          </Route>
          <Route path="/">
            <Home />
          </Route>
        </Switch>
      </div>
    </Router>
  );
}
