import React, { useState } from "react";
// import MyHeader from "./components/header";
import { makeStyles } from "@material-ui/core/styles";
import video from "./model.mp4";
import LabelBottomNavigation from "./components/footer";

const useStyles = makeStyles((theme) => ({
  video: {
    width: "50%",
    marginLeft: "auto",
    marginRight: "auto",
    marginTop: "50px",
    display: "block",
  },
  headers: {
    textAlign: "center",
    marginTop: "100px",
    fontFamily: "Mate SC",
  },
}));

function Home(props) {
  const classes = useStyles();
  return (
    <React.Fragment>
      {/* <MyHeader></MyHeader> */}
      <h2 className={classes.headers}>
        We know your expectations about clothes
      </h2>
      <div>
        <video autoPlay={true} muted loop className={classes.video}>
          <source src={video} type="video/mp4" />
        </video>
      </div>
      <LabelBottomNavigation />
    </React.Fragment>
  );
}

export default Home;
