import React, { useState, useContext, useEffect } from "react";
// import MyHeader from "./components/header";
import { makeStyles } from "@material-ui/core/styles";
import LabelBottomNavigation from "./components/footer";
import { UserContext } from "./App";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import Button from "@material-ui/core/Button";

const useStyles = makeStyles((theme) => ({
  headers: {
    textAlign: "center",
    marginTop: "100px",
    fontFamily: "Mate SC",
    paddingBottom: "3%",
  },
  outheaders: {
    textAlign: "center",
    marginTop: "100px",
    fontFamily: "Mate SC",
    paddingBottom: "3%",
    color: "red",
  },
  subheaders: {
    textAlign: "center",
    fontFamily: "Mate SC",
  },
  table: {
    minWidth: 650,
  },
  tableCon: {
    width: "90%",
    marginLeft: "auto",
    marginRight: "auto",
  },
  myBtn: {
    marginLeft: "10px",
  },
}));

function ShoppingCart(props) {
  const classes = useStyles();
  const { user, setUser } = useContext(UserContext);
  const { REACT_APP_API_URL } = process.env;
  const [checkout, setCheckout] = useState(false);
  console.log(user);

  function getCart() {
    //get cart
    console.log("getting cart");
    console.log(user._id);
    fetch(`${REACT_APP_API_URL}/api/carts/myCart/${user._id}`, {
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      method: "GET",
    })
      .then(function (res) {
        res.json().then((data) => {
          console.log(data);
          console.log("get carts");
          !data.message && setUser((prev) => ({ ...prev, cart: data }));
        });
      })
      .catch(function (res) {
        console.log(res);
      });
    console.log(user);
  }

  useEffect(() => {
    getCart();
  }, []);

  function removeItem(name) {
    let newCart = user.cart[0].product.filter((x) => x.name !== name);
    fetch(`${REACT_APP_API_URL}/api/carts/myCart/`, {
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        Authorization: `Bearer ${user.token}`,
      },
      method: "POST",
      body: JSON.stringify({
        user: user._id,
        product: newCart,
      }),
    })
      .then(function (res) {
        res.json().then((data) => {
          !data.message && setUser((prev) => ({ ...prev, cart: [data] }));
          console.log(user);
        });
      })
      .catch(function (res) {
        console.log(res);
      });
  }

  return (
    <React.Fragment>
      <h2 className={classes.headers}>Your items in cart</h2>
      {!user.cart && (
        <h4 className={classes.subheaders}>Please login to see your cart</h4>
      )}
      {user.cart && !user.cart[0] && (
        <h4 className={classes.subheaders}>Please add items to your cart</h4>
      )}
      {user.cart[0] && user.cart[0].product[0] && !checkout && (
        <>
          <TableContainer component={Paper} className={classes.tableCon}>
            <Table className={classes.table} aria-label="simple table">
              <TableHead>
                <TableRow>
                  <TableCell>Name</TableCell>
                  <TableCell align="right">Category</TableCell>
                  <TableCell align="right">Price</TableCell>
                  <TableCell align="right">Remove</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {user.cart[0].product.map((row) => (
                  <TableRow key={row.name}>
                    <TableCell component="th" scope="row">
                      {row.name}
                    </TableCell>
                    <TableCell align="right">{row.category}</TableCell>
                    <TableCell align="right">{row.price}</TableCell>
                    <TableCell align="right">
                      <Button
                        variant="contained"
                        color="secondary"
                        onClick={() => {
                          removeItem(row.name);
                        }}
                      >
                        x
                      </Button>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
            <div className={classes.myBtn}>
              <Button
                variant="contained"
                color="primary"
                onClick={() => {
                  setCheckout(true);
                }}
              >
                Check Out
              </Button>
            </div>
          </TableContainer>
        </>
      )}
      {checkout && (
        <h2 className={classes.outheaders}>
          Thank you so much for shopping with us. We will ship your order soon.
        </h2>
      )}

      <LabelBottomNavigation />
    </React.Fragment>
  );
}

export default ShoppingCart;
